import { Box, Button, InputAdornment, Stack, TextField, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { useEffect, useMemo, useState } from 'react'
import { Link } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getMailings } from 'redux/slices/Mailing'
import SearchIcon from '@mui/icons-material/Search'

export default function Mailing() {
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const status = useAppSelector((s) => s.Mailing.status)
	const list = useAppSelector((s) => s.Mailing.list)
	const [search, setSearch] = useState('')

	const dispatch = useAppDispatch()
	useEffect(() => {
		if (status === 'init') dispatch(getMailings())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	const data = useMemo(() => {
		return list.filter((f) => f.mailMembers.find((m) => m.email.includes(search)))
	}, [list, search])
	if (!tokenInfo) return null
	return (
		<Stack gap={2}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					メーリングリスト一覧
				</Typography>
				<Box flex={1} maxWidth={300}>
					<TextField
						variant="outlined"
						margin="dense"
						fullWidth
						size="small"
						InputProps={{
							startAdornment: (
								<InputAdornment position="start">
									<SearchIcon />
								</InputAdornment>
							),
						}}
						label={'フィルター'}
						InputLabelProps={{ shrink: true }}
						value={search}
						onChange={(e) => setSearch(e.target.value)}
					/>
				</Box>
				<Link to="/mailing/create">
					<Button
						variant="outlined"
						sx={{
							minWidth: 120,
						}}
					>
						新規作成
					</Button>
				</Link>
			</Stack>
			<DataGrid
				rows={data}
				columns={[
					{
						field: 'name',
						headerName: 'メールグループ名',
						flex: 1,
						renderCell: (v) => {
							return (
								<Link to={`/mailing/${v.row.id}`}>
									<Typography variant="caption" mr={1}>
										{v.row.name}
									</Typography>
								</Link>
							)
						},
					},
					{
						field: 'email',
						headerName: 'メンバー一覧',
						flex: 2,
						renderCell: (v) => {
							return (
								<Typography
									variant="caption"
									mr={1}
									sx={{
										maxWidth: '100%',
										overflow: 'hidden',
										textOverflow: 'ellipsis',
										whiteSpace: 'nowrap',
									}}
									component="p"
									dangerouslySetInnerHTML={{
										__html: v.row.mailMembers
											.map((m) => m.email)
											.sort((a, b) => {
												const aIn = a.indexOf(search)
												const bIn = b.indexOf(search)
												return bIn - aIn
											})
											.join(',')
											.replaceAll(search, `<span style="color:#a00;background:#ff2b;">${search}</span>`),
									}}
								></Typography>
							)
						},
					},
					{ field: 'type', headerName: '式', width: 100 },
				]}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 10 },
					},
				}}
				pageSizeOptions={[10, 20, 30, 50]}
				sx={{
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:hover': {},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				density="compact"
				hideFooterSelectedRowCount
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
